<template>
  <div class="All">
    <div v-for="stream in streams" v-bind:key="stream.alias">
      <div class="video-container" @click="playFullscreen">
        <hls-video :playsinline="true" :controls="false" :video-src="streamUri + '/' + stream.streamUri" />
        <div class="stream-label">
          {{ stream.alias }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      getListURI: "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort + "/list",
      stopURI: "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort + "/stop",
      startURI: "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort + "/start",
      streamUri: "http://" + window.destinations.activeDomain + ":" + window.destinations.activePort,
      streams: null,
    };
  },
  methods: {
    async getStreams() {
      const response = await fetch(this.getListURI,{
        method: "GET",        
        credentials: 'include'
      });      
      return response.json();
    },
    playFullscreen(e){      
      var vidEl = e.currentTarget.querySelector('video');
      vidEl.webkitSetPresentationMode('fullscreen');
      vidEl.addEventListener('webkitendfullscreen',()=>{
        setTimeout(() => {
          vidEl.play();
        }, 1000);
      })
    }
  },
  async mounted() {
    this.streams = await this.getStreams();
  },
};
</script>

<style lang="less">
#app {
  overflow: auto;
}
.All {
  color: white;
  .video-container {
    position: relative;

    .stream-label {
      position: absolute;
      top: 0px;
      left: 0px;
      background-color: black;
      padding: 4px;
      text-transform: uppercase;
      font-size: 10px;
    }
  }
}
</style>